lcome to Happywed.</h1>                  \r\n                <p v-if=\"url === 'host'\">As a host, you can create and manage wedding events, track RSVPs, and communicate with your guests all in one place. Sign up now to start your wedding planning journey.</p>   \r\n                <p v-else-if=\"url === 'vendor'\" >We're thrilled to have you join our community of wedding professionals. Our platform connects you with couples who are seeking your services, making it easier for you to showcase your skills and grow your business. Sign up now to get started and be a part of the Happywed family.</p>     \r\n                <p v-else>Welcome! Wedding Community Members</p>       \r\n                <form\r\n                    @submit.prevent=\"loginUser($event)\"\r\n                >\r\n                    <!-- <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" /> -->\r\n                    <label for=\"name\" class=\"login-form-group\">\r\n                        Full Name\r\n                        <input\r\n                            type=\"text\"\r\n                            name=\"name\"\r\n                            id=\"name\"\r\n                            v-model=\"feilds.name\"\r\n                        />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.name\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.name[0] }}</span\r\n                        >\r\n                    </label>\r\n                    <div class=\"double-form\">\r\n                    <label for=\"email\" class=\"login-form-group\">\r\n                        E-Mail Address\r\n                        <input\r\n                            type=\"email\"\r\n                            name=\"email\"\r\n                            id=\"email\"\r\n                            v-model=\"feilds.email\"\r\n                        />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.email\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.email[0] }}</span\r\n                        >\r\n                    </label>        \r\n                       <label for=\"mobile\" class=\"login-form-group\">\r\n                                Mobile Number\r\n                                <input\r\n                                    type=\"mobile\"\r\n                                    name=\"mobile\"\r\n                                    id=\"mobile\"\r\n                                    v-model=\"feilds.mobile\"\r\n                                />\r\n                                <span\r\n                                    v-if=\"errorSubmit && errorSubmit.mobile\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errorSubmit.mobile[0] }}</span\r\n                                >\r\n                                </label>    \r\n                    </div>                   \r\n                    <div v-if=\"url != 'admin'\" >\r\n                        <!-- <label for=\"state_name\" class=\"login-form-group\">\r\n                            State\r\n                            <select\r\n                                name=\"state_name\"\r\n                                id=\"state_name\"\r\n                                @change=\"getDistricts($event, true)\"\r\n                                v-model=\"state_name\"\r\n                            >\r\n                                <option disabled selected value=\"null\">\r\n                                    Select States\r\n                                </option>\r\n                                <option\r\n                                    v-for=\"(item, index) in stateList\"\r\n                                    :key=\"index\"\r\n                                    :value=\"item\"\r\n                                >\r\n                                    {{ item }}\r\n                                </option>\r\n                            </select>\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.state_name\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.state_name[0] }}</span\r\n                            >\r\n                        </label> -->\r\n                           <Dropdown class=\"form-control\" v-model=\"feilds.city\" :options=\"cities\" id=\"city\" name= \"city\" optionLabel=\"name\" placeholder=\"City\" :filter=\"true\" filterPlaceholder=\"Find City\"/>\r\n                             <span\r\n                                v-if=\"errorSubmit && errorSubmit.city\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.city[0] }}</span\r\n                                >\r\n                        <!-- <div v-if=\"errors.city\" class=\"text-danger\" v-text=\"errors.city\"></div> -->\r\n                        \r\n                        <!-- <label for=\"location_id\" class=\"login-form-group\">\r\n                            City\r\n                            <select\r\n                                name=\"location_id\"\r\n                                id=\"location_id\"\r\n                                v-model=\"location_id\"\r\n                                size=\"4\"\r\n                            >  -->\r\n                                <!-- <option\r\n                                    v-if=\"state_name == null\"\r\n                                    disabled\r\n                                    selected\r\n                                    value=\"null\"\r\n                                >\r\n                                    Select state first\r\n                                </option>\r\n                                <option v-else disabled selected value=\"null\">\r\n                                    Select city\r\n                                </option> -->\r\n                                 <!-- <option value=\"\" disabled>selectLocation</option>\r\n                               <option\r\n                                    v-for=\"(item, index) in locations\"\r\n                                    :key=\"index\"\r\n                                    :value=\"item.id\"\r\n                                >\r\n                                    {{ item.name }}\r\n                                </option>\r\n                            </select>\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.location_id\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.location_id[0] }}</span\r\n                            >\r\n                        </label> -->\r\n                    </div>\r\n                    \r\n                \r\n                    <div class=\"double-form\">\r\n                        <!-- <label for=\"mobile\" class=\"login-form-group\">\r\n                            Mobile Number\r\n                            <input\r\n                                type=\"mobile\"\r\n                                name=\"mobile\"\r\n                                id=\"mobile\"\r\n                                v-model=\"mobile\"\r\n                            />\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.mobile\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.mobile[0] }}</span\r\n                            >\r\n                        </label> -->\r\n                        <label for=\"mobile\" class=\"login-form-group\">\r\n                            <input\r\n                                hidden\r\n                                v-model=\"feilds.role\"\r\n                                type=\"text\"\r\n                                name=\"role\"\r\n                                id=\"role\"\r\n                            />\r\n                            <!-- <select name=\"role\" id=\"role\" v-model=\"role\">\r\n                                <option selected value=\"host\">Host</option>\r\n                                <option value=\"company\">Wedding Planner</option>\r\n                                <option value=\"vendor\">Vendor</option>\r\n                            </select> -->\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.role\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.role[0] }}</span\r\n                            >\r\n                        </label>\r\n                    </div>\r\n                       <div class=\"double-form\">\r\n                        <label for=\"password\" class=\"login-form-group\">\r\n                            Password\r\n                            <input\r\n                                :type=\"showPass\"\r\n                                name=\"password\"\r\n                                id=\"password\"\r\n                                autocomplete\r\n                                v-model=\"feilds.password\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'text'\"\r\n                                v-if=\"showPass == 'password'\"\r\n                                src=\"/assets/login/eye.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'password'\"\r\n                                v-if=\"showPass == 'text'\"\r\n                                src=\"/assets/login/eye-off.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <span\r\n                                style=\"margin-top: 9px\"\r\n                                v-if=\"errorSubmit && errorSubmit.password\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.password[0] }}</span\r\n                            >\r\n                        </label>\r\n                        <label for=\"password_confirmation\" class=\"login-form-group\">\r\n                            Confirm Password\r\n                            <input\r\n                                :type=\"showPass\"\r\n                                name=\"password_confirmation\"\r\n                                id=\"password_confirmation\"\r\n                                autocomplete\r\n                                v-model=\"feilds.password_confirmation\"\r\n                            />\r\n                            <span\r\n                                v-if=\"\r\n                                    errorSubmit && errorSubmit.password_confirmation\r\n                                \"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.password_confirmation[0] }}</span\r\n                            >\r\n                            <img\r\n                                @click=\"showPass = 'text'\"\r\n                                v-if=\"showPass == 'password'\"\r\n                                src=\"/assets/login/eye.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'password'\"\r\n                                v-if=\"showPass == 'text'\"\r\n                                src=\"/assets/login/eye-off.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </label>\r\n                    </div>\r\n                    <div class=\"forn-grp-dbl\">\r\n                        <label\r\n                            for=\"policy\"\r\n                            class=\"login-form-group\"\r\n                            style=\"flex-direction: row\"\r\n                        >\r\n                            <input\r\n                                type=\"checkbox\"\r\n                                value=\"true\"\r\n                                id=\"policy\"\r\n                                v-model=\"feilds.policy\"\r\n                            />\r\n                            <span\r\n                                >I have read the\r\n                                <a :href=\"route('termsandconditions')\">terms and conditions</a> and agreed\r\n                                with <a :href=\"route('privacypolicy')\">privacy policy</a></span\r\n                            >\r\n                        </label>\r\n                    </div>\r\n                    <button type=\"submit\" :disabled=\"disableLogIn\">\r\n                        Sign Up\r\n                    </button>\r\n                </form>\r\n                <div class=\"sinup-link-cont\">\r\n                    Already have an Account, Login in as\r\n                    <a v-if=\"url == 'host'\" :href=\"route('hostLogin')\"\r\n                        >Log in</a\r\n                    >\r\n                    <a v-if=\"url == 'vendor'\" :href=\"route('vendorLogin')\"\r\n                        >Log in</a\r\n                    >\r\n                     <a v-if=\"url == 'admin'\" :href=\"route('adminLogin')\"\r\n                            >Log in</a\r\n                        >\r\n                </div>\r\n                <!-- <div class=\"login-toggle-cont\">\r\n                    <a :href=\"route('hostLogin')\" v-if=\"active != 'host'\">\r\n                        Host\r\n                    </a>\r\n                    <a :href=\"route('login')\" v-if=\"active != 'guest'\">\r\n                        Guest\r\n                    </a>\r\n                    <a :href=\"route('brideLogin')\" v-if=\"active != 'bride'\">\r\n                        Bride\r\n                    </a>\r\n                    <a :href=\"route('groomLogin')\" v-if=\"active != 'groom'\">\r\n                        Groom\r\n                    </a>\r\n                    <a :href=\"route('vendorLogin')\" v-if=\"active != 'vendor'\">\r\n                        Vendor\r\n                    </a>\r\n                    <a :href=\"route('companyLogin')\" v-if=\"active != 'company'\">\r\n                        Planner\r\n                    </a>\r\n                    <a :href=\"route('memberLogin')\" v-if=\"active != 'member'\">\r\n                        Member\r\n                    </a>\r\n                </div> -->\r\n            </div>\r\n        </div>\r\n        <div class=\"login-pics-cont\">\r\n            <div class=\"splide\" id=\"login-splide-cont\">\r\n                <div class=\"splide__track\">\r\n                    <ul class=\"splide__list\">\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img1.png');\r\n                                \"                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Moments made memorable for a unique love\r\n                                    story\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img6.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Every love story is beautiful, but yours\r\n                                    should be unique Making events come alive\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img7.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>Make Any Occasion Unforgettable.</p>\r\n                            </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n            <img src=\"/assets/login/top-right.png\" alt=\"\" class=\"ab-img\" />\r\n        </div>\r\n        <FlashMessage :message=\"message\"></FlashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport FlashMessage from \"@/components/FlashMessage.vue\";\r\nimport Dropdown from \"primevue/Dropdown\";\r\n\r\nexport default {\r\n    props: [\"errorSubmit\", \"oldvalues\", \"locations\", \"cities\", \"url\"],\r\n    components: {\r\n        FlashMessage, Dropdown\r\n    },\r\n    data() {\r\n        return {\r\n            message: null,\r\n            splider: null,\r\n            active: \"Host\",\r\n            disableLogIn: false,\r\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\r\n            locationMaster: null,\r\n            stateList: [],\r\n            district: [],\r\n            hideDefaultOption: true,\r\n            feilds: {\r\n                name: this.oldvalues[\"name\"] ?? null,\r\n                email: this.oldvalues[\"email\"] ?? null,\r\n                //state_name: this.oldvalues[\"state_name\"] ?? null,\r\n                city: this.oldvalues[\"city\"] ?? null,\r\n                mobile: this.oldvalues[\"mobile\"] ?? null,\r\n                role: this.url,\r\n                policy: true,\r\n                password: null,\r\n                password_confirmation: null,\r\n            },\r\n            // name: this.oldvalues[\"name\"] ?? null,\r\n            // email: this.oldvalues[\"email\"] ?? null,\r\n            // //state_name: this.oldvalues[\"state_name\"] ?? null,\r\n            // location_id: this.oldvalues[\"location_id\"] ?? null,          \r\n            // city : this.oldvalues[\"city\"] ?? null,\r\n            // mobile: this.oldvalues[\"mobile\"] ?? null,\r\n            // role: this.url,\r\n            // policy: true,\r\n            // password: null,\r\n            // password_confirmation: null,\r\n            showPass: \"password\",\r\n        };\r\n    },\r\n    methods: {\r\n        submitForm(e) {   \r\n            console.log(e, 'ss');\r\n            if (this.policy == false) {\r\n                this.message = \"Accept terms and conditions first!!\";\r\n                setTimeout(function () {\r\n                    this.message = null;\r\n                }, 3000);\r\n            } else {\r\n                this.disableLogIn = true;\r\n                e.currentTarget.submit();\r\n            }\r\n        },\r\n        loginUser(e) {\r\n            console.log(this.active);\r\n            let _this = this;\r\n            _this.disableLogIn = true;\r\n            let formData = new FormData();\r\n            let link = route(\"hostRegister\");\r\n            // for(let keys in this.feilds){\r\n            //     formData.append(keys, _this.feilds[keys]);\r\n            // }\r\n            // formData.append(\"email\", _this.email);\r\n            // formData.append(\"password\", _this.password);\r\n            // formData.append(\"remember_token\", _this.remember_token);\r\n            axios({\r\n                url: link,\r\n                method: \"POST\",\r\n                data: _this.feilds,\r\n            })\r\n                .then((response) => {\r\n                    console.log(response.data);\r\n                    window.location.href = response.data.redirect;\r\n                  //  window.location.reload();\r\n                })\r\n                .catch((error) => {\r\n                    _this.errorSubmit = error.response.data.errors;\r\n                    console.log(error);\r\n                });\r\n        },\r\n        spliderEvent() {\r\n            let _this = this;\r\n            this.splider = new Splide(\"#login-splide-cont\", {\r\n                type: \"slide\",\r\n                arrows: false,\r\n                pagination: true,\r\n                gap: \"10px\",\r\n                width: \"100%\",\r\n                perPage: 1,\r\n                perMove: 1,\r\n                autoplay: true,\r\n                interval: 3000,\r\n                rewind: true,\r\n                pauseOnHover: true,\r\n            });\r\n            this.splider.mount();\r\n        },\r\n        // getStates() {\r\n        //     var _this = this;\r\n        //     _this.locationMaster = _this.locations;\r\n        //     _this.stateList = [\r\n        //         ...new Set(_this.locationMaster.map((item) => item.state)),\r\n        //     ];\r\n        //     if (this.state_name != null && this.state_name != \"\") {\r\n        //         this.getDistricts(_this.state_name, false);\r\n        //     }\r\n        // },\r\n        // getDistricts(e, bool) {\r\n        //     let _this = this;\r\n        //     let val = null;\r\n        //     if (bool == true) {\r\n        //         val = e.currentTarget.value;\r\n        //     }\r\n        //     if (bool == false) {\r\n        //